import React from 'react';

const $ = window.$;

export default class HomeSession extends React.PureComponent {
  componentDidMount() {
    /* ---------------------------------------------- /*
     * Home Banner Scroll 
    /* ---------------------------------------------- */
    $(".lets-talk").click(function () {
      $('html,body').animate({
        scrollTop: $("#contact").offset().top
      },
        'slow');
    });
    
    /* ---------------------------------------------- /*
     * home Banner
    /* ---------------------------------------------- */
    $(".home-banner").height($(window).height());
    $(window).resize(function () {
      $(".home-banner").height($(window).height());
    });
    $('.home-banner').parallax({ imageSrc: './images/figma.jpg' });

    /* ---------------------------------------------- /*
     * particlesJS
    /* ---------------------------------------------- */
    window.particlesJS('home', {
      "particles": {
        "number": {
          "value": 80,
          "density": {
            "enable": true,
            "value_area": 800
          }
        },
        "color": {
          "value": "#ffffff"
        },
        "shape": {
          "type": "circle",
          "stroke": {
            "width": 0,
            "color": "#000000"
          },
          "polygon": {
            "nb_sides": 5
          },
          "image": {
            "src": "img/github.svg",
            "width": 100,
            "height": 100
          }
        },
        "opacity": {
          "value": 0.5,
          "random": false,
          "anim": {
            "enable": false,
            "speed": 1,
            "opacity_min": 0.1,
            "sync": false
          }
        },
        "size": {
          "value": 5,
          "random": true,
          "anim": {
            "enable": false,
            "speed": 40,
            "size_min": 0.1,
            "sync": false
          }
        },
        "line_linked": {
          "enable": true,
          "distance": 150,
          "color": "#ffffff",
          "opacity": 0.4,
          "width": 1
        },
        "move": {
          "enable": true,
          "speed": 6,
          "direction": "none",
          "random": false,
          "straight": false,
          "out_mode": "out",
          "attract": {
            "enable": false,
            "rotateX": 600,
            "rotateY": 1200
          }
        }
      },
      "interactivity": {
        "detect_on": "canvas",
        "events": {
          "onhover": {
            "enable": true,
            "mode": "repulse"
          },
          "onclick": {
            "enable": true,
            "mode": "push"
          },
          "resize": true
        },
        "modes": {
          "grab": {
            "distance": 400,
            "line_linked": {
              "opacity": 1
            }
          },
          "bubble": {
            "distance": 400,
            "size": 40,
            "duration": 2,
            "opacity": 8,
            "speed": 3
          },
          "repulse": {
            "distance": 200
          },
          "push": {
            "particles_nb": 4
          },
          "remove": {
            "particles_nb": 2
          }
        }
      },
      "retina_detect": true,
      "config_demo": {
        "hide_card": false,
        "background_color": "#b61924",
        "background_image": "",
        "background_position": "50% 50%",
        "background_repeat": "no-repeat",
        "background_size": "cover"
      }
    }

    );
  }
  render = () => (
    <section id="home" className="home-banner" data-parallax="scroll">
      <div className="container">
        <div className="welcome-text">
          <h3>Hello! i'm</h3>
          <h2>Tran Viet Hoa</h2>
          <p>FRONT-END WEB DEVELOPMENT</p>
          <div className="wt-actions">
            <button className="m-btn lets-talk">Let's talk New</button>
          </div>
        </div>
      </div>
    </section>
  )
}